import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { AdvancedSearchService, IPagination, IPlayer } from '@aft-core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { generateFullPublicModuleRoute } from '@scouting/public/public.routes';
import { PRIVATE_MODULE_ROUTES } from '@scouting/private/private.routes';
import { Router } from '@angular/router';

@Component({
  selector: 'aft-scout-search-results',
  templateUrl: './search-results.component.html',
  styleUrls: ['./search-results.component.scss'],
})
export class SearchResultsComponent {
  players$: Observable<IPlayer[]>;
  pagination$: Observable<IPagination>;

  sortOptions: Array<any>;

  form: FormGroup;

  constructor(private _advSearchService: AdvancedSearchService, private _fb: FormBuilder, private _router: Router) {
    this.players$ = this._advSearchService.players$.asObservable();
    this.pagination$ = this._advSearchService.pagination$.asObservable();
    this.sortOptions = this._advSearchService.sortOptions;

    this.form = this._fb.group({
      sortBy: [this.sortOptions[0].value],
    });
  }

  goBack(): void {
    this._router.navigate(generateFullPublicModuleRoute(PRIVATE_MODULE_ROUTES.HOME));
  }

  pageChanged($event: any): void {
    this._advSearchService.doSearch($event.first);
    window.scrollTo(0, 0);
  }
}
