import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ENTITIES_ENUM, IPlayer, ITeam } from '@aft-core';
import { generateFullPrivateModuleRoute, PRIVATE_MODULE_ROUTES } from '@scouting/private/private.routes';
import { FormBuilder, FormControl } from '@angular/forms';
import { FIXTURE_MODULE_ROUTES, generateFullFixtureModuleRoute } from '@sw-ui-components/modules/fixture/fixture.routes';

@Component({
  selector: 'aft-scout-player-list-item',
  templateUrl: './player-list-item.component.html',
  styleUrls: ['./player-list-item.component.scss'],
})
export class PlayerListItemComponent implements OnInit {
  @Input() player: IPlayer | undefined;

  playerTotalStars: FormControl;
  entitiesEnum = ENTITIES_ENUM;

  constructor(private _router: Router, private _fb: FormBuilder) {
    this.playerTotalStars = this._fb.control(undefined);
  }

  ngOnInit(): void {
    this.playerTotalStars = this._fb.control({ value: this.player?.totalScore || 0, disabled: true });
  }

  goToPlayer(player: IPlayer | undefined): void {
    this._router.navigate(generateFullPrivateModuleRoute(PRIVATE_MODULE_ROUTES.PLAYER, player?.id));
  }

  goToTeam(team: ITeam | undefined): void {
    if (team) {
      this._router.navigate(generateFullFixtureModuleRoute(FIXTURE_MODULE_ROUTES.TEAM_DETAILS, team.id));
    }
  }
}
