// eslint-disable-next-line @typescript-eslint/naming-convention
import { APP_ROUTES } from '@aft-core';

// eslint-disable-next-line @typescript-eslint/naming-convention
export const ADVANCED_SEARCH_ROUTES = {
  ADVANCED_SEARCH: '',
};

export const generateAdvancedSearchModuleRoute = (route: string): Array<string> => {
  if (route?.length) {
    return [route];
  }
  return [];
};

export const generateFullPrivateModuleRoute = (route: string): Array<string> => {
  return [...APP_ROUTES.PRIVATE, ...generateAdvancedSearchModuleRoute(route)];
};
