import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ADVANCED_SEARCH_ROUTES } from '@scouting/private/advanced-search/advanced-search.routes';
import { AdvancedSearchComponent } from '@scouting/private/advanced-search/components/advanced-search/advanced-search.component';

const routes: Routes = [
  {
    path: ADVANCED_SEARCH_ROUTES.ADVANCED_SEARCH,
    component: AdvancedSearchComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdvancedSearchRoutingModule {}
