import { Component, OnDestroy, OnInit } from '@angular/core';
import {
  AdvancedSearchService,
  COUNTRIES_ENUM_ARRAY,
  IArea,
  ILeague,
  ITeam,
  LeaguesService,
  LoadingService,
  PLAYER_FOOT_ENUM,
  PLAYER_ROLES_ARRAY,
  TeamsPlayersService,
} from '@aft-core';
import { BehaviorSubject, Subscription } from 'rxjs';
import { AbstractControl, FormBuilder, FormGroup } from '@angular/forms';

@Component({
  selector: 'aft-scout-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.scss'],
})
export class SideBarComponent implements OnInit, OnDestroy {
  form: FormGroup;
  loading: boolean = true;

  // Filters
  leagues$: BehaviorSubject<Array<ILeague>>;
  teams$: BehaviorSubject<Array<ITeam>>;
  playerRoles = PLAYER_ROLES_ARRAY;
  countries: IArea[] = COUNTRIES_ENUM_ARRAY;
  footList = Object.values(PLAYER_FOOT_ENUM).map(foot => ({ name: foot, translationKey: `PLAYER.FOOT.${foot?.toUpperCase()}` }));
  // End filter

  private _subscriptions: Array<Subscription>;

  constructor(
    private _loadingService: LoadingService,
    private _fb: FormBuilder,
    private _leaguesService: LeaguesService,
    private _teamsService: TeamsPlayersService,
    private _advSearchService: AdvancedSearchService
  ) {
    this._subscriptions = [];

    this._subscriptions.push(
      this._loadingService.loading$.subscribe(isLoading => {
        this.loading = isLoading;
      })
    );

    this.leagues$ = this._leaguesService.leagues$;
    this.teams$ = this._teamsService.teams$;

    this.form = this._fb.group(this._advSearchService.loadFilterValues());
  }

  resetForm(): void {
    this.form.reset(this._advSearchService.resetFilterValues());
  }

  ngOnInit(): void {
    this._subscriptions.push(
      this.leagueCtrl.valueChanges.subscribe((league: ILeague) => {
        if (league?.id) {
          if (league.id !== this.form.value.league?.id) {
            // Clean teams values when selected league has changed
            this.teamsCtrl.setValue(null);
          }
          this._teamsService.loadTeamsByLeagueId(league.id);
          this.teamsCtrl.enable();
        } else {
          this.teamsCtrl.setValue(null);
          this.teamsCtrl.disable();
        }
      })
    );

    this._subscriptions.push(
      this.form.valueChanges.subscribe(formValues => {
        this._advSearchService.formValues = formValues;
      })
    );
  }

  ngOnDestroy(): void {
    this._subscriptions.forEach(sub => sub.unsubscribe());
  }

  onSubmit(): void {
    this._advSearchService.doSearch();
  }

  get playerNameCtrl(): AbstractControl {
    return this.form.controls.playerName;
  }

  get leagueCtrl(): AbstractControl {
    return this.form.controls.league;
  }

  get teamsCtrl(): AbstractControl {
    return this.form.controls.teams;
  }

  get countryCtrl(): AbstractControl {
    return this.form.controls.country;
  }

  get passportCtrl(): AbstractControl {
    return this.form.controls.passport;
  }

  get ageCtrl(): AbstractControl {
    return this.form.controls.age;
  }

  get heightCtrl(): AbstractControl {
    return this.form.controls.height;
  }

  get weightCtrl(): AbstractControl {
    return this.form.controls.weight;
  }
}
